@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
html,body{height:100%;background-color:#3f3f3f}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#root{height:100%}

main{display:flex;flex-wrap:wrap;justify-content:center;height:100%}

.spellcard{margin:0.1rem;padding:0.2rem;background-color:#e70000;border-radius:5px;box-shadow:2px 2px 4px 2px rgba(0,0,0,0.2);display:grid;grid-template-columns:1fr 1fr;grid-template-rows:-webkit-min-content -webkit-min-content -webkit-min-content -webkit-min-content 2.8fr -webkit-min-content -webkit-min-content;grid-template-rows:min-content min-content min-content min-content 2.8fr min-content min-content;grid-gap:1px 1px;gap:1px 1px;grid-template-areas:"name name" "castingTime range" "components duration" "materials materials" "description description" "higher higher" "footer footer"}.section{background-color:#f1f1f1;border-radius:5px;margin:0.2rem;padding:0.3rem}.spellcard-name{grid-area:name;text-align:center;padding:0;padding-top:.4rem;padding-bottom:.4rem}.spellcard-castingTime{grid-area:castingTime;text-align:center}.spellcard-range{grid-area:range;text-align:center}.spellcard-components{grid-area:components;text-align:center}.spellcard-duration{grid-area:duration;text-align:center}.spellcard-materials{grid-area:materials;color:white;padding:0.1rem;padding-left:0.3rem}.spellcard-description{grid-area:description;overflow:auto;scrollbar-width:thin}.spellcard-description::-webkit-scrollbar{width:7px}.spellcard-description::-webkit-scrollbar-track{background:#f1f1f1}.spellcard-description::-webkit-scrollbar-thumb{background:#cdcdcd}.spellcard-description::-webkit-scrollbar-thumb:hover{background:#555}.spellcard-higher{grid-area:higher}.spellcard-footer{grid-area:footer}.footer{display:flex;justify-content:space-between;align-items:center;color:white;text-align:right;padding:0.2rem}.footer p{font-size:.72rem}.rmwc-tooltip-inner{font-size:1.08rem;font-weight:bold}.material-collapse{text-decoration:underline;cursor:pointer}.rmwc-collapsible-list__handle{cursor:pointer}.material-collapse-child{padding:0.2rem;margin-top:0.1rem;max-width:100%;border:1px solid #f1f1f1;border-radius:5px;font-style:italic}.name-tooltip{font-size:1.1rem;text-align:center}h1{margin:0;font-size:1.2rem}p{font-size:.9rem;margin:0}p.materials{font-style:italic}h2{margin:0;font-size:.9rem;font-weight:bold}span{font-size:.9rem}

.spell-table{margin-left:300px;transition:margin-left 0.5s ease-in-out;padding:1rem;flex:1 1}.spells-wide{margin-left:60px;transition:margin-left 0.5s ease-in-out}.spell-table-window{display:flex;justify-content:center}@media (max-width: 1255px){.spell-table{margin-left:0;margin-top:60px}.spells-wide{margin-left:0;margin-top:60px}}

.sidebar{width:300px;height:100%;transition:width 0.5s ease-in-out;background-color:#1b1b1b;position:fixed;left:0;display:flex;flex-direction:column;overflow-y:auto;overflow-x:hidden}.sidebar-hidden{transition:width 0.5s ease-in-out;width:60px}.sidebar-last-spells{display:flex;align-items:left;margin:10px;margin-top:auto;color:white;font-family:'Roboto'}.sidebar-filters{opacity:1;transition:opacity 0.5s ease-in-out;display:flex;flex-direction:column}.sidebar-filters-hidden{opacity:0;transition:opacity 0.5s ease-in-out}.sidebar-button{width:60px;height:60px;font-size:2rem}.sidebar-component{flex:1 1;margin:10px;font-size:1rem;color:white}.sidebar-component ul{width:260px}.sidebar-component.MuiFormControl-root{margin:10px}.sidebar-component.MuiList-root{background-color:#424242;margin:10px;border-radius:3px}.sidebar-component.MuiList-padding{padding:0}.levels-component-selects{margin:0;display:flex;flex-direction:row}.levels-component-selects .MuiFormControl-root{flex:1 1}.components-collapsible{color:white;background-color:#424242;border-radius:5px 5px 0 0;margin-bottom:0}.components-collapsible-child{display:flex;justify-content:space-around}.components-collapsible-child .MuiListItem-gutters{padding:0}.components-tooltip{font-size:0.8rem}.schools-collapsible-child{margin:0 10px 0 10px}.MuiMenu-paper{transition-duration:0s !important}@media (max-width: 1255px){.sidebar{width:100%;transition:height 0.5s ease-in-out;z-index:10;left:auto;left:initial;top:0}.sidebar-filters{flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center}.sidebar-component{flex:1 1 280px;max-width:450px}.sidebar-hidden{height:60px;transition:height 0.5s ease-in-out}}@media (max-width: 600px){.sidebar{height:750px}.sidebar-hidden{height:60px}}@media (min-width: 601px) and (max-width: 900px){.sidebar{height:470px}.sidebar-hidden{height:60px}}@media (min-width: 900px) and (max-width: 1255px){.sidebar{height:330px}.sidebar-hidden{height:60px}}

